.formulario {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2% 0;
}

.formulario .ciudadContainer {
  width: 80%;
  height: auto;
  position: relative;
}

.formulario .ciudadContainer .ciudadInput {
  font-family: "Roboto", sans-serif;
  color: #fff5dd;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  width: 100%;
  height: 100%;
  padding: 2% 1%;
  border: none;
  border-bottom: solid 2px #fff5dd;
  outline: none;
  background-color: transparent;
  transition: all 0.3s ease-in-out;
}

.formulario .ciudadContainer .ciudadInput:valid ~ .labelCity span {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}

.formulario .ciudadContainer .ciudadInput:focus {
  border-bottom: solid 2px #00d1ff;
}

.formulario .ciudadContainer .ciudadInput:focus ~ .labelCity span {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}

.formulario .ciudadContainer .labelCity {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.formulario .ciudadContainer .labelCity span {
  position: absolute;
  margin-left: 2%;
  color: #fff5dd;
  left: 0;
  bottom: 5px;
  transition: all 0.3s ease-in-out;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 42px;
}

.formulario .paisesInput {
  width: 80%;
  color: #fff5dd;
  margin-top: 5%;
  margin-bottom: 5%;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 42px;
  background-color: transparent;
  border: none;
  outline: none;
  border-bottom: solid 2px #fff5dd;
}

.formulario .paisesInput option {
  color: black;
}

.formulario .errorMessage {
  color: yellow;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  margin-bottom: 2%;
}

.formulario .boton {
  width: 70%;
  color: #fff5dd;
  border: none;
  padding: 2% 0;
  border-radius: 50px;
  background-color: #1aaf8b;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
}

@media screen and (max-width: 400px) {
  .formulario {
    margin-top: 7%;
  }
  .formulario .ciudadContainer .labelCity span {
    font-size: 18px;
  }
  .formulario .paisesInput {
    font-size: 18px;
    margin-bottom: 10%;
  }
  .formulario .paisesInput option {
    font-size: 18px;
  }
  .formulario .errorMessage {
    font-size: 12px;
  }
  .formulario .boton {
    font-size: 18px;
  }
}
